import React, { Component } from "react";
import AreaProvinsiKota from "./Component/AreaProvinsiKota";
import Table from "./Component/Table";
import { editKota, getKotaList } from "../actions/kotaAction";
import { connect } from "react-redux";
import notrespond from "../Images/500.svg";
import Swal from "sweetalert2";
import FormKotaEdit from "./Component/FormKotaEdit";
import { reset } from "redux-form";
import Skeleton from "react-loading-skeleton";
import { AxiosMasterDelete, AxiosMasterPost, AxiosMasterPut } from "../Axios";
import { berhasil, gagal, LogData, reactive } from "../Alert";
import { hideModal, showModal } from "../actions/ModalAction";
import ModalMasterData from "./Component/ModalMasterData";
import HeaderCard from "../HeaderCard";
import TourGuide from "../TourGuide";
import { finishSend, progressSend } from "../actions/LogicAction";

function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      AxiosMasterDelete("kota/delete/" + kode)
        .then(() => dispatch(hideModal()))
        .then((res) => berhasil("Data Berbasil Dihapus"))
        .then(() => dispatch(getKotaList()))
        .catch((err) => gagal("Data Gagal Dihapus"));
    }
  });
}

const defaultSorted = [
  {
    dataField: "kodeKota",
    order: "asc",
  },
];
const mapToState = (state) => {
  return {
    KotaList: state.provinsi.kota,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
    onSend: state.provinsi.onSend,
  };
};

class Kota extends Component {
  constructor(props) {
    super(props);
    this.state = {
      provinsi: [],
      provinsi_array: [],
      response: "",
      columns: [
        {
          dataField: "kode_kota",
          text: "Kode Kota",
          sort: true,
          headerStyle: () => {
            return { width: "15%" };
          },
        },
        {
          dataField: "nama_kota",
          text: "Nama Kota",
          sort: true,
        },
        {
          dataField: "kode_provinsi",
          text: "Provinsi",
          sort: true,
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => hapus(this.props.dispatch, row.kode_kota)}
                  data-tut="reactour_3"
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  className="btn btn-warning mr-2"
                  data-tut="reactour_4"
                  onClick={() =>
                    this.props.dispatch(
                      editKota(
                        row.kode_kota,
                        row.nama_kota,
                        row.kode_provinsi,
                        row.status_aktif,
                        this.editModal()
                      )
                    )
                  }
                >
                  <i className="fa fa-edit mr-1"></i>
                  EDIT
                </button>
              </div>
            );
          },
        },
      ],
    };
  }
  componentDidMount() {
    this.props.dispatch(getKotaList());
  }

  handlerSubmit(data) {
    const isi = {
      nama_kota: data.NamaKota,
      kode_kota: data.KodeKota,
      kode_provinsi: data.kodeProvinsi,
    };
    this.props.dispatch(progressSend());
    console.log(isi);
    AxiosMasterPost("kota/add", isi)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berasil Ditambahkan ke Kota")
          .then(() => LogData("Data Berhasil Ditambahkan ke Kota", data))
          .then(() => this.props.dispatch(getKotaList()))
      )
      .catch((err) =>
        this.handleReactive(err, data.KodeKota, {
          nama_kota: data.NamaKota,
          kode_provinsi: data.kodeProvinsi,
        })
      );
  }
  handlerEdit(data) {
    const isi = {
      statusAktif: data.status ? data.status : "AKTIF",
      namaKota: data.NamaKota,
      kodeKota: data.KodeKota,
      kodeProvinsi: data.kodeProvinsi,
    };
    this.props.dispatch(progressSend());
    console.log(isi);
    AxiosMasterPut("kota/edit/" + data.KodeKota, {
      nama_kota: data.NamaKota,
      kode_provinsi: data.kodeProvinsi,
    })
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Dirubah").then(() =>
          this.props.dispatch(getKotaList())
        )
      )
      .catch((err) =>
        gagal("Data Gagal Ditambahkan ke Kota")
          .then(() => this.props.dispatch(getKotaList()))
          .then(() => this.props.dispatch(reset("Provinsi")))
      );
  }
  handleReactive(err, kode, data) {
    this.props.dispatch(hideModal());
    let error = err.response.data;
    let check = error.includes("deleted");
    check
      ? reactive(err, kode, "kota/reactive/", data, "kota/edit/").then(() =>
          this.props.dispatch(getKotaList())
        )
      : gagal("Data Gagal Ditambahkan");
  }
  editModal() {
    this.props.dispatch(showModal());
    this.setState({ isEdit: true });
  }
  tambahModal() {
    this.props.dispatch(showModal());
    this.setState({ isEdit: false });
  }

  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true">Ã—</span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <TourGuide
          tourConfig={[
            `Untuk Menambahkan Data Kota, Tekan Tombol ini Dan Isi kolom yang disediakan`,
            `Ini adalah data Kota yang sudah tersimpan`,
            `Ini adalah tombol untuk menghapus data pada baris yang dipilih`,
            `Ini adalah tombol untuk mengubah data pada baris yang dipilih`,
          ]}
        />
        <ModalMasterData
          content={
            this.state.isEdit ? (
              <FormKotaEdit
                nama="Nama Kota"
                kode="Kode Kota"
                kode_ext="Nama Provinsi"
                onSubmit={(data) => this.handlerEdit(data)}
                from="kota"
                onSend={this.props.onSend}
              />
            ) : (
              <AreaProvinsiKota
                nama="Nama Kota"
                kode="Kode Kota"
                kode_ext="Nama Provinsi"
                onSubmit={(data) => this.handlerSubmit(data)}
                from="kota"
                onSend={this.props.onSend}
              />
            )
          }
          title={this.state.isEdit ? "Edit Data Kota" : "Tambah Data Kota"}
        />
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Data Kota" />
              <div className="card-body">
                <div className="col-lg-12 col-md-12">
                  <div className="text-right">
                    <button
                      name="simpan"
                      id="simpan"
                      type="button"
                      className="btn btn-info"
                      onClick={() => this.tambahModal()}
                      data-tut="reactour_1"
                    >
                      Tambah Data Kota
                    </button>
                  </div>
                </div>
                <div className="col-lg-12" data-tut="reactour_2">
                  {this.props.KotaList ? (
                    <Table
                      data={this.props.KotaList}
                      columns={this.state.columns}
                      defaultSorted={defaultSorted}
                      namaKey="kodeKota"
                    />
                  ) : (
                    <div>
                      {this.props.error ? (
                        <div className="text-center">
                          <img
                            src={notrespond}
                            style={{ width: "30%" }}
                            alt="ACTIVE"
                          />
                          <br />
                          <h4>Request API Gagal, Silahkan Refresh</h4>
                        </div>
                      ) : (
                        <Skeleton width={"100%"} height={180} />
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(mapToState, null)(Kota);
